import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from '../core/auth.service';
import { ServiceProviderService } from '../core/service-provider.service';

@Component({
  selector: 'app-pet-seaters',
  templateUrl: './pet-seaters.page.html',
  styleUrls: ['./pet-seaters.page.scss'],
})
export class PetSeatersPage implements OnInit {
  data;
  constructor(
    private activatedRoute: ActivatedRoute,
    private providerService: ServiceProviderService
  ) {
    console.log('working');
  }

  ngOnInit() {}
  ionViewWillEnter() {
    this.getData();
  }

  getData() {
    console.log('working');
    let id = this.activatedRoute.snapshot.queryParams.serviceId;
    this.providerService.getServiceByCategoryId(id).subscribe((res) => {
      this.data = res;
    });
  }
}
